
.movie-card {
    box-sizing: border-box;
    width: 100%;
    max-width: 280px;
    height: auto;
    border-radius: 20px;
    margin: 20px auto; 
  }
  
 
  .movie-card .movie-title {
    color: #000;
    font-family: 'Roboto', sans-serif;
    font-size: 1em;
    font-weight: 500;
    text-transform: uppercase;
    margin: 10px 0 0 0;
  }
  
 
  .movie-card .movie-info {
    color: #b92f2c;
    font-family: 'Roboto', sans-serif;
    font-size: 1em;
    font-weight: 500;
    line-height: 1.3em;
    margin: 0;
  }
  

  .movie-card .movie-image {
    width: 100%; 
    height: auto; 
    object-fit: cover; 
    box-shadow: 0 4px 8px 0 rgba(2, 1, 1, 0.2),
      0 6px 20px 0 rgba(36, 33, 33, 0.19);
    border-radius: 10px;
    transition: transform 0.3s ease;
    cursor: pointer;
  }
  
  .movie-card .movie-image:hover,
  .movie-card .movie-image:focus {
    transform: scale(1.03);
  }
  
 
  .fixedbutton {
    width: 40px;
    height: 40px;
    border: none;
    background-color: transparent;
    position: fixed;
    bottom: 245px;
    right: 20px;
    transition: transform 0.3s ease;
  }
  
  .fixedbutton img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
  
  .fixedbutton:hover {
    cursor: pointer;
    transform: scale(1.3);
  }
  
  .fixedbutton:focus {
    transform: scale(1.3);
  }
  
  @media screen and (min-width: 768px) {
    .movie-card {
      display: inline-block;
      margin: 10px; 
      max-width: 395px;
    }
  
    .movie-card:nth-child(odd) {
      margin-right: 10px; 
    }
  
    .movie-card:nth-child(even) {
      margin-left: 10px; 
    }
  
    
    .movie-container {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      min-width: 900px;
    }
  
    .fixedbutton {
      bottom: 125px;
    }
  }
  
  @media screen and (min-width: 1280px) {
    .movie-container {
      margin: 10px auto;
      
    }
    .movie-card {
      max-width: 395px;
    }
  }
  