.movie-card {
  box-sizing: border-box;
  width: 100%;
  max-width: 280px;
  height: auto;
  border-radius: 20px;
  margin: 20px auto;
}

.movie-card .movie-title {
  color: #000;
  text-transform: uppercase;
  margin: 10px 0 0;
  font-family: Roboto, sans-serif;
  font-size: 1em;
  font-weight: 500;
}

.movie-card .movie-info {
  color: #b92f2c;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 1em;
  font-weight: 500;
  line-height: 1.3em;
}

.movie-card .movie-image {
  width: 100%;
  height: auto;
  object-fit: cover;
  cursor: pointer;
  border-radius: 10px;
  transition: transform .3s;
  box-shadow: 0 4px 8px rgba(2, 1, 1, .2), 0 6px 20px rgba(36, 33, 33, .19);
}

.movie-card .movie-image:hover, .movie-card .movie-image:focus {
  transform: scale(1.03);
}

.fixedbutton {
  width: 40px;
  height: 40px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  transition: transform .3s;
  position: fixed;
  bottom: 245px;
  right: 20px;
}

.fixedbutton img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.fixedbutton:hover {
  cursor: pointer;
  transform: scale(1.3);
}

.fixedbutton:focus {
  transform: scale(1.3);
}

@media screen and (min-width: 768px) {
  .movie-card {
    max-width: 395px;
    margin: 10px;
    display: inline-block;
  }

  .movie-card:nth-child(2n+1) {
    margin-right: 10px;
  }

  .movie-card:nth-child(2n) {
    margin-left: 10px;
  }

  .movie-container {
    min-width: 900px;
    flex-wrap: wrap;
    justify-content: center;
    display: flex;
  }

  .fixedbutton {
    bottom: 125px;
  }
}

@media screen and (min-width: 1280px) {
  .movie-container {
    margin: 10px auto;
  }

  .movie-card {
    max-width: 395px;
  }
}

